<script>
import TableComponent from "@/components/Table/Table.vue";
import Pagination1 from "@/components/Pagination1/Pagination1.vue";

export default {
  name: "memberManagementComponent",
  components: { TableComponent, Pagination1 },
  data() {
    return {
      organizeData: {},
      obj: {},
      queryForm: {
        PO_ID: "",
        S_ID: "",
        S_NAME: "",
        valid: "",
        authorization: [],
        IN_DATE1: "",
        IN_DATE2: "",
        OUT_DATE1: "",
        OUT_DATE2: "",
        inDate: [],
        outDate: [],
        ISWORK: "",
        ISNOTWORK: "",
      },
      rules: {
        S_ID: [{ required: false, message: "必填", trigger: "blur" }],
        S_NAME: [{ required: false, message: "必填", trigger: "blur" }],
        valid: [{ required: false, message: "必填", trigger: "change" }],
        authorization: [
          { required: false, message: "必填", trigger: "change" },
        ],
        inDate: [{ required: false, message: "必填", trigger: "change" }],
        outDate: [{ required: false, message: "必填", trigger: "change" }],
      },
      tableColumns: [
        {
          id: "index",
          label: "項次",
          width: "70",
        },
        {
          id: "s_Id",
          label: "承攬商編號",
          width: "150",
        },
        {
          id: "s_Name",
          label: "承攬商名稱",
          width: "150",
        },
        {
          id: "in_Date",
          label: "入會日期",
          width: "150",
        },
        {
          id: "out_Date",
          label: "退會日期",
          width: "150",
        },
        {
          id: "Status",
          label: "當前狀態",
          width: "150",
        },
        {
          id: "qualifications",
          label: "資格分類",
          width: "150",
        },
      ],
      selectedMember: [],
      dialogVisible: false,
      memberInfoForm: {
        s_Id: "",
        s_Name: "",
        tel: "",
        key_Man: "",
        group: true,
        authorization: [],
      },
      memberInfoRules: {
        group: [{ required: true, message: "必填", trigger: "change" }],
        authorization: [
          { required: false, message: "必填", trigger: "change" },
        ],
      },
      tableData: [],
      tableList: [],
      work_Group: {},
    };
  },
  computed: {},
  methods: {
    async exportProtocal() {
      this.queryForm.PO_ID = this.organizeData.po_id;
      let item = JSON.stringify(this.queryForm);
      let res = item.replace(/""/g, null);
      let data = JSON.parse(res);
      try {
        const res = await this.$store.dispatch("exportProtocal", data);
        const href = URL.createObjectURL(res.data);
        const link = document.createElement("a");
        link.href = href;
        link.setAttribute("download", `${this.organizeData.po_Name}.xlsx`);
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(href);
      } catch (err) {
        console.log(err);
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    setForm() {
      if (!this.queryForm.inDate) {
        this.queryForm.inDate = [];
      }

      if (!this.queryForm.outDate) {
        this.queryForm.outDate = [];
      }

      if (this.queryForm.inDate.length) {
        this.queryForm.IN_DATE1 = this.queryForm.inDate[0];
        this.queryForm.IN_DATE2 = this.queryForm.inDate[1];
      } else {
        this.queryForm.IN_DATE1 = "";
        this.queryForm.IN_DATE2 = "";
      }

      if (this.queryForm.outDate.length) {
        this.queryForm.OUT_DATE1 = this.queryForm.outDate[0];
        this.queryForm.OUT_DATE2 = this.queryForm.outDate[1];
      } else {
        this.queryForm.OUT_DATE1 = "";
        this.queryForm.OUT_DATE2 = "";
      }

      if (
        this.queryForm.authorization.length &&
        this.queryForm.authorization.includes("F")
      ) {
        this.queryForm.F_AUTHORIZATION = "Y";
      } else {
        this.queryForm.F_AUTHORIZATION = "";
      }

      if (
        this.queryForm.authorization.length &&
        this.queryForm.authorization.includes("S")
      ) {
        this.queryForm.S_AUTHORIZATION = "Y";
      } else {
        this.queryForm.S_AUTHORIZATION = "";
      }

      if (this.queryForm.valid === "") {
        this.queryForm.ISWORK = "";
        this.queryForm.ISNOTWORK = "";
      } else if (this.queryForm.valid === "1") {
        this.queryForm.ISWORK = "Y";
        this.queryForm.ISNOTWORK = "";
      } else {
        this.queryForm.ISWORK = "";
        this.queryForm.ISNOTWORK = "Y";
      }
      this.queryForm.PO_ID = this.organizeData.po_id;
      // Object.keys(this.queryForm).forEach(
      //   (e) =>
      //     (this.queryForm[e] === null || this.queryForm[e] === undefined) &&
      //     delete this.queryForm[e]
      // );
    },
    async submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          this.$refs.TableComponent.$refs.multipleTable.clearSelection();
          this.setForm();
          await this.queryMember(this.queryForm);
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    changeSelection(item) {
      this.selectedMember = item;
      this.selectedMember.forEach((e) => {
        e.po_Id = this.organizeData.po_id;
        e.workGroup_Id = this.work_Group.workgroup_Id;
      });
    },
    createMemberHandler() {
      this.$router.push({
        name: "createMember",
        params: { po_Id: this.organizeData.po_id },
        query: { po_Name: this.organizeData.po_Name },
      });
    },
    async deleteHandler() {
      const res = await this.$store.dispatch("delMember", this.selectedMember);
      if (res.data.isOK) {
        this.$message({
          type: "success",
          duration: 5000,
          showClose: true,
          message: "刪除成功",
        });
        await this.queryMember(this.obj);
        this.$store.state.currentPage = 1;
        this.$refs.TableComponent.$refs.multipleTable.clearSelection();
      }
    },
    editHandler(item) {
      this.dialogVisible = true;
      this.memberInfoForm = { ...this.memberInfoForm, ...item };
      if (item.f_Authorization === "Y" && item.S_Authorization === "Y") {
        this.memberInfoForm.authorization = ["F", "S"];
      } else if (item.f_Authorization === "Y" && item.S_Authorization !== "Y") {
        this.memberInfoForm.authorization = ["F"];
      } else if (item.f_Authorization !== "Y" && item.S_Authorization === "Y") {
        this.memberInfoForm.authorization = ["S"];
      } else {
        this.memberInfoForm.authorization = [];
      }
      this.memberInfoForm.workGroup_Id = this.work_Group.workgroup_Id;
      this.memberInfoForm.po_Id = this.organizeData.po_id;
    },
    handleClose(done) {
      done();
    },
    async submitDialog(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          if (this.memberInfoForm.authorization.length) {
            if (this.memberInfoForm.authorization.includes("F")) {
              this.memberInfoForm.f_Authorization = "Y";
            } else {
              this.memberInfoForm.f_Authorization = null;
            }

            if (this.memberInfoForm.authorization.includes("S")) {
              this.memberInfoForm.S_Authorization = "Y";
            } else {
              this.memberInfoForm.S_Authorization = null;
            }
          } else {
            this.memberInfoForm.f_Authorization = null;
            this.memberInfoForm.S_Authorization = null;
          }
          const res = await this.$store.dispatch(
            "editMember",
            this.memberInfoForm
          );
          if (res.data.isOK) {
            this.$message({
              type: "success",
              duration: 5000,
              showClose: true,
              message: "修改成功",
            });
            this.dialogVisible = false;
            await this.queryMember(this.obj);
            this.$store.state.currentPage = 1;
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    changePage(page) {
      this.$store.state.currentPage = page;
      const cur = (page - 1) * 10;
      const last = page * 10;
      this.tableList = this.tableData.slice(cur, last);
    },
    async queryMember(obj) {
      const params = this.getURLNewParams(obj);
      const res = await this.$store.dispatch("queryMember", params);
      const data = res.data.data;
      this.tableData = JSON.parse(JSON.stringify(data.members));
      this.tableData.forEach((e, index) => {
        e.index = index + 1;
        if (e.f_Authorization === "Y" && e.S_Authorization === "Y") {
          e.qualifications = `主包 下包`;
        } else if (e.f_Authorization === "Y" && e.S_Authorization !== "Y") {
          e.qualifications = `主包`;
        } else if (e.f_Authorization !== "Y" && e.S_Authorization === "Y") {
          e.qualifications = `下包`;
        } else {
          e.qualifications = ``;
        }
      });
      this.tableList = this.tableData.slice(0, 10);
      this.work_Group = data.work_Group;
    },
  },
  async mounted() {
    this.$store.state.currentPage = 1;
    this.organizeData = JSON.parse(this.$route.query.data);
    this.obj = {
      PO_ID: this.organizeData.po_id,
      S_ID: "",
      S_NAME: "",
      IN_DATE1: "",
      IN_DATE2: "",
      OUT_DATE1: "",
      OUT_DATE2: "",
      F_AUTHORIZATION: "",
      S_AUTHORIZATION: "",
      ISWORK: "",
      ISNOTWORK: "",
    };
    this.queryMember(this.obj);
    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;
  },
};
</script>

<template src="./memberManagement.html"></template>
